body {
  background-color: #003142;
  color: #777;
  font-size: 10px;
  padding-bottom: 9rem;
}

#footer {
  font-size: 3rem;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: 1000;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.25);
  height: 4rem;
}

#footer > * {
  vertical-align: middle;
}

#result {
  display: block;
  width: 100%;
  text-align: center;
  position: absolute;
  top: 0;
  z-index: -1;
}

#footer.error {
  background-color: maroon;
  color: #c8f0ff;
}

.close {
  display: block;
  position: fixed;
  top: 1rem;
  right: 1rem;
  background-color: #fff;
  border-radius: 50%;
  color: #333;
  z-index: 1000;
  font-size: 1.5rem;
  font-weight: bold;
  width: 2rem;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  text-decoration: none;
}
