#coins {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
}

.coinWrapper {
  width: 20rem;
  height: 10rem;
  display: block;
  font-weight: bold;
  font-size: 3em;
  line-height: 10rem;
  margin: 1rem auto 0;
  z-index: 1;
  position: relative;
}

.coinWrapper > *,
.coinWrapper > *:before,
.coinWrapper > *:after {
  vertical-align: middle;
  display: inline-block;
}
.coin {
  border-radius: 50%;
  background-color: transparent;
  margin: 0 auto;
  width: 10rem;
  height: 10rem;
  line-height: 10rem;
  z-index: 10;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.coinSubtract,
.coinAdd {
  background-color: #fff;
  border: 1px solid #333;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  line-height: 0.5rem;
  text-indent: -0.2rem;
  padding: 1rem;
  position: relative;
  left: 1rem;
}

.coinSubtract {
  text-indent: 0;
}

.coinAdd {
  position: relative;
  left: 12rem;
}

.count {
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  color: #77ee77;
  background-color: #333;
  position: relative;
  left: 4.5rem;
  top: 2.5rem;
  text-align: center;
  z-index: 1000;
  border-radius: 50%;
  visibility: hidden;
  pointer-events: none;
}

#save {
  background-color: #333;
  border-color: transparent;
  border-radius: 50%;
  font-size: 2.25rem;
  text-indent: -0.6rem;
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  width: 3rem;
  height: 3rem;
  line-height: 2.75rem;
}
#save:active {
  outline: none;
  border: none;
  text-indent: -0.4rem;
  line-height: 3.25rem;
  box-shadow: inset 5px 4px 9px 0px rgba(0, 0, 0, 0.9);
}

#save:focus {
  outline: none;
}
