#purchase {
  color: #777;
  font-size: 3rem;
  width: 8rem;
  text-align: right;
  height: 4rem;
  border: 0;
  position: absolute;
  padding: 0 10px;
  right: 6rem;
}

#purchase:active,
#purchase:focus {
  outline: none;
}

#purchaseIcon {
  position: absolute;
  right: 1rem;
}

.coinStack {
  width: 24rem;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
}
.coinStack ol {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  list-style: none;
}

.coinStack li {
  /* background: linear-gradient(
    to right,
    rgba(51, 51, 51, 0.9) 0%,
    rgba(255, 255, 255, 0) 25%,
    rgba(255, 255, 255, 0) 75%,
    rgba(51, 51, 51, 0.9) 100%
  ); */
  background-color: #18bd18;
  width: 8rem;
  height: 1rem;
  margin: 0.2rem;
}

.coinStack li.andABit {
  height: 0.25rem;
  margin: 0 0.1rem;
}

.coinStack .price li {
  background-color: #d10000;
}

@media (min-height: 768px) {
  .coinStack li {
    width: 12rem;
    height: 1.5rem;
    margin: 0.3rem;
  }
}

@media (min-height: 1024px) {
  .coinStack li {
    width: 16rem;
    height: 2rem;
    margin: 0.4rem;
  }
}
